export const parseDate = (dateStr) => {
    if (dateStr && typeof dateStr === 'string' && dateStr.length > 0) {
        const [ yyyy, mm, dd ] = dateStr.split('.');
        return { year: yyyy, month: mm, date: dd };
    }
    return null;
};

export const formatTime = (timeStr) => {
    if (!timeStr || typeof timeStr !== 'string' || timeStr.length <= 0)
        return "";
    const [hour, minute] = timeStr.split(':');
    if (Number(hour) < 12) return ` ${hour}:${minute}`;
    if (Number(hour) === 12) return ` ${hour}:${minute}`;
    const afternoon = Number(hour) - 12;
    if (afternoon < 10) return ` 0${afternoon}:${minute}`;
    return ` ${afternoon}:${minute}`;
};

export const formatDate = (dateStr) => {
    if (!dateStr || typeof dateStr !== 'string' || dateStr.length <= 0)
        return "";
    const [year, month, day] = dateStr.split('.');
    return `${year} ${month} ${day}`;
};
