import React from 'react';
import { useNavigate } from 'react-router';
import './ArtistListPage.css';

/**
 * Categorize modal component.
 * @param {number} artistNum 
 * @param {Function} showModal
 * @param {boolean} isHidden
 * @returns Categorize modal component
 */
function CategorizeModal({ artistNum, showModal, isHidden }) {
    const navigate = useNavigate();

    const onClickCategory = (e, category) => {
        // Prevent bubbling
        e.preventDefault();
        e.stopPropagation();
        // Navigate by categorization
        if (category === 1) navigate(`/fromm/${artistNum}`);
        // TODO: Categorize by date
        // if (category === 2) navigate(`/fromm/${artistNum}/date`);
    };

    const onCancelCategory = () => {
        showModal(null);
    };

    return (
        <div className={`categorize-modal-bg flex-center select-none ${isHidden ? "hidden" : ""}`} onClick={onCancelCategory}>
            <div className="categorize-modal flex-col">
                <div className="category-btn flex-center" onClick={(e) => onClickCategory(e, 1)}>  </div>
                <div className="category-btn flex-center" onClick={(e) => onClickCategory(e, 2)}>  </div>
            </div>
        </div>
    );
}

export default CategorizeModal;