import React, { useEffect, useState } from 'react';
import { useNavigate } from 'react-router-dom';
import { readString } from 'react-papaparse';
import Header from '../../Header/Header';
import './ChatListPage.css';

/**
 * Chat preview component.
 * @param {string} date: date of the chat 
 * @returns Chat preview component
 */
function ChatPreview({ date }) {
    const navigate = useNavigate();

    const onPreview = () => {
        navigate(`/vlive/${date}`);
    };

    return (
        <div className="chat-preview" onClick={onPreview}><h3>{date}</h3></div>
    );
}

/**
 * Chat list page component.
 * @returns Chat list page component
 */
function ChatListPage() {
    const [ChatList, setChatList] = useState([]);

    // Fetch list of chats by date
    useEffect(() => {
        const textCSV = require(`../../../assets/vlive/text.csv`);
        readString(textCSV, papaConfig);
    });

    // Helper function to read list of chats by date
    const papaConfig = {
        complete: (results, file) => {
            let dataCSV = results.data;
            let chatList = [];
            for (let i = 1; i < dataCSV.length; i++) {
                if (!chatList.includes(dataCSV[i][0])) {
                    chatList.push(dataCSV[i][0]);
                }
            }
            setChatList(chatList);
        },
        download: true,
        error: (error, file) => {
            console.log('Error while parsing:', error, file);
        },
    };

    return (
        <div className="mobile vlive select-none">
            <Header url="/" />
            <div className="vlive-chat-list flex-col">
                <h2>Loona+ Chats</h2>
                {ChatList && ChatList.length > 0 && ChatList.map((date, index) => (
                    <ChatPreview key={`vlive-chat-preview-${index}`} date={date} />
                ))}
            </div>
        </div>
    );
}

export default ChatListPage;