import React, { useEffect, useState } from 'react';
import { useLocation } from 'react-router-dom';
import { readString } from 'react-papaparse';
import Header from '../../Header/Header';
import Profile from './Profile';
import ChatBubble from './ChatBubble';
import { formatTime } from '../../../lib/date';
import './ChatPage.css';
import Date from './Date';

function Chats({ artistNum, chatData }) {
    const chatBubble = chatData.map((chat, index) => {
        if (chat[0] === "" || chat[2] === ""
            || chat[3] === "" || chat[4] === ""
            || chat[5] === "" || chat[6] === "") return;

        // TODO: Put date component if date is changed
        const chatDate = () => {
            if (index !== 0) {
                const prevChat = chatData[index - 1];
                if (prevChat[3] === "Default" || prevChat[0] !== chat[0]) {
                    return <Date date={chat[0]} />;
                }
            }
        };

        return (
            <div key={`fromm-chat-${artistNum}-${index}`} className="chat">
                {chatDate()}
                <div className="chat-piece flex-row">
                    <Profile artistNum={artistNum} imgNum={chat[4]} />
                    <div className="chat-r">
                        <p className="artist-name select-none">{chat[6]}</p>
                        <div className="chat-r-b flex-row">
                            <ChatBubble artistNum={artistNum} dateStr={chat[0]} text={chat[2]} type={chat[3]} />
                            <p className="bubble-time flex-col select-none">{formatTime(chat[1])}</p>
                        </div>
                    </div>
                </div>
            </div>
        );
    });
    
    return (
        <div className="chat">
            {chatData && chatData.length > 0 && chatBubble}
        </div>
    );
}

/**
 * Chat page component.
 * @returns Chat page component
 */
function ChatPage() {
    const location = useLocation();

    const [ArtistNum, setArtistNum] = useState("");
    const [CSVText, setCSVText] = useState([]);
    
    useEffect(() => {
        const artistNum = location.pathname.split('/')[2];
        setArtistNum(artistNum);
    }, [location.pathname]);

    useEffect(() => {
        if (ArtistNum === "") return;

        const papaConfig = {
            complete: (results, file) => {
                let dataCSV = results.data;
                // Get text data
                if (dataCSV && dataCSV.length > 0)
                    setCSVText(dataCSV.slice(1));
            },
            download: true,
            error: (error, file) => {
                console.log('Error while parsing:', error, file);
            },
        };

        const textCSV = require(`../../../assets/fromm/${ArtistNum}/text.csv`);
        readString(textCSV, papaConfig);
    }, [ArtistNum]);

    return (
        <div className="mobile fromm">
            <Header url={'/fromm'} />
            <div className="chatpage">
                <Chats artistNum={ArtistNum} chatData={CSVText} />
            </div>
        </div>
    );
}

export default ChatPage;