import { useEffect, useState } from 'react';
import { useLocation, useNavigate } from 'react-router-dom';
import { readString } from 'react-papaparse';
import { convertDate } from '../../../App';
import Header from '../../Header/Header';
import './ArtistPage.css';

const artistName = [
    "  HeeJin", "  HyunJin", "  HaSeul", "  YeoJin", 
    "  ViVi", "  Kim Lip", "  JinSoul", "  Choerry", 
    "  Yves", "  Chuu", "  Go Won", "   Olivia Hye"
];

function FabMsgImg({ artistNum, data }) {
    const navigate = useNavigate();
    
    const onClickThumbnail = (msgNum) => {
        navigate(`/fab/${artistNum}/${msgNum}`);
    };

    // Message without media
    if (data.data[0] !== "(Video)" && !data.data[0].includes("Image")) {
        const emptyImg = require(`../../../assets/fab/empty.jpg`);
        return (
            <div className="artist-msg select-none" onClick={() => onClickThumbnail(data.msgNum)}>
                <p>{convertDate(data.data[3].slice(0, 10))}</p>
                <img src={emptyImg} alt="" />
            </div>
        );
    }

    let msgNum = data.msgNum;
    if (msgNum.length === 1) msgNum = "0" + msgNum;
    let artistMedia = null;
    if (data.data[0] === "(Video)") {
        // Message with a video
        const artistVideo = require(`../../../assets/fab/${artistNum}/media/${msgNum}_0.mp4`);
        artistMedia = <video width="750" height="500" >
            <source src={artistVideo} type="video/mp4"/>
        </video>;
    } else {
        // Message with an image
        const artistImg = require(`../../../assets/fab/${artistNum}/media/${msgNum}_0.jpg`);
        artistMedia = <img src={artistImg} alt="" />;
    }

    return (
        <div className="artist-msg select-none" onClick={() => onClickThumbnail(data.msgNum)}>
            <p>{convertDate(data.data[3].slice(0, 10))}</p>
            {artistMedia}
        </div>
    );
}

function ArtistPage() {
    const location = useLocation();

    const [ArtistNum, setArtistNum] = useState("");
    const [ProfileImg, setProfileImg] = useState("");
    const [BGImg, setBGImg] = useState("");
    const [CSVText, setCSVText] = useState([]);

    // Get artist profile info
    useEffect(() => {
        const chatId = location.pathname.split('/')[2];
        setArtistNum(chatId);
        const profileImg = require(`../../../assets/fab/${chatId}/profile/0.jpg`);
        setProfileImg(profileImg);
        const bgImg = require(`../../../assets/fab/${chatId}/bg/0.jpg`);
        setBGImg(bgImg);
    }, [location.pathname]);

    useEffect(() => {
        if (ArtistNum === "") return;
        // Helper function to read list of chats by message
        const papaConfig = {
            complete: (results, file) => {
                let dataCSV = results.data;
                // Get text data by message
                let dataByMsg = [];
                let i = 0;
                while (i < dataCSV[0].length) {
                    if (dataCSV[0][i] === "") break;
                    // Get text data by line
                    dataByMsg.push({ 
                        msgNum: dataCSV[0][i], 
                        data: dataCSV[2].slice(i, i + 4),
                    });
                    i += 4;
                }
                setCSVText(dataByMsg);
            },
            download: true,
            error: (error, file) => {
                console.log('Error while parsing:', error, file);
            },
        };

        // Fetch list of chats by message
        const textCSV = require(`../../../assets/fab/${ArtistNum}/text.csv`);
        readString(textCSV, papaConfig);
    }, [ArtistNum]);
    

    const fabMsgGrid = () => {
        const rows = [];
        for (let i = 0; i < CSVText.length; i += 3) {
            rows.push(<div key={`artist-msg-row-${i / 3}`} className="flex-row">
                {i < CSVText.length && <FabMsgImg key={`artist-msg-${i}`} artistNum={ArtistNum} data={CSVText[i]} />}
                {i + 1 < CSVText.length 
                    ? <FabMsgImg key={`artist-msg-${i + 1}`} artistNum={ArtistNum} data={CSVText[i + 1]} /> 
                    : <div className="artist-msg select-none"></div>
                }
                {i + 2 < CSVText.length 
                    ? <FabMsgImg key={`artist-msg-${i + 2}`} artistNum={ArtistNum} data={CSVText[i + 2]} /> 
                    : <div className="artist-msg select-none"></div>
                }
            </div>);
        }
        return rows;
    }

    return (
        <div className="artistpage flex-col flex-center select-none">
            <Header url="/fab" />
            <div className="artistpage-body">
                <div className="top">
                    <img src={ProfileImg} className="artist-profile" alt="" />
                    <div className="artist-background">
                        <img src={BGImg} alt="" />
                    </div>
                    <div className="profile-name flex-row">
                        <p>{artistName[ArtistNum - 1]}</p>
                    </div>
                </div>
                <div className="artist-body flex-col">
                    {fabMsgGrid()}
                </div>
            </div>
        </div>
    );
}

export default ArtistPage;