import { useEffect, useState } from 'react';
import { useLocation, useNavigate } from 'react-router-dom';
import { readString } from 'react-papaparse';
// Component
import MediaSlide from './MediaSlide';
// Style
import { CommentOutlined } from '@ant-design/icons';
import './MessagePage.css';
import Header from '../../Header/Header';

const artistName = [
    "  HeeJin", "  HyunJin", "  HaSeul", "  YeoJin", 
    "  ViVi", "  Kim Lip", "  JinSoul", "  Choerry", 
    "  Yves", "  Chuu", "  Go Won", "   Olivia Hye"
];

const countReply = (data, index) => {
    // i, i + 4
    let count = 0;
    while (count < data.length && data[count][index] !== "") {
        count++;
    }
    return count;
};

// Get index of first style
const minStyleIndex = (text, styleType) => {
    let styleIndex = -1;
    let firstStyle = "";
    for (let col = 0; col < styleType.length; col++) {
        const index = text.indexOf(`(${styleType[col]})`);
        if (index >= 0 && (index < styleIndex || styleIndex < 0)) {
            styleIndex = index;
            firstStyle = styleType[col];
        }
    }
    return firstStyle;
};

// Color text
const styleText = (text) => {
    // Get index of first style
    const firstStyle = minStyleIndex(text, ["small", "big", "blue", "green", "red", "pink"]);
    // If text is stylized
    if (firstStyle !== "") {
        let colList = text.split(`(${firstStyle})`);
        const colText = colList[1].split(`(/${firstStyle})`);
        if (colList.length > 2) {
            return <span>
                {colList[0]}
                <span className={`msg-text-${firstStyle}`}>{
                    firstStyle === "big" ? styleText(colText[0]) : colText[0]
                }</span>
                {styleText(colText[1] + colList.slice(2).join(`(${firstStyle})`))};
            </span>;
        } else {
            return <span>
                {colList[0]}
                <span className={`msg-text-${firstStyle}`}>{
                    firstStyle === "big" ? styleText(colText[0]) : colText[0]
                }</span>
                {styleText(colText[1])}
            </span>;
        }
    }
    return text;
};

/**
 * Message component that has styled text.
 * @param {*} chatId 
 * @param {*} CSVText
 * @param {string} artistNum: number of current artist
 * @param {Function} onOpenMedia: function to open media modal when clicked
 * @returns Message component
 */
function Message({ chatId, CSVText, artistNum, onOpenMedia }) {
    // Stylize message
    if (chatId === "" || !CSVText || CSVText === [] || CSVText.length === 0) {
        return <p></p>;
    }
    if (CSVText[0] === "") {
        // Message without anything
        return <p></p>;
    }
    // Video
    if (CSVText[0] === "(Video)") {
        return (
            <div className="artist-msg video">
                <video width="750" height="500" controls autoPlay >
                    <source src={require(`../../../assets/fab/${artistNum}/media/${chatId.length === 1 ? "0" + chatId : chatId}_0.mp4`)} type="video/mp4"/>
                </video>
            </div>
        );
    }
    // Full Image
    if (CSVText[0] === "(Full Image)") {
        return (
            <div className="artist-msg full-img">
                <img src={require(`../../../assets/fab/${artistNum}/media/${chatId.length === 1 ? "0" + chatId : chatId}_0.jpg`)} alt="" />
            </div>
        ); 
    }
    // If text or image text
    const msgText = CSVText[0].split('\n');
    let imgCount = 0;
    for (let i = 0; i < msgText.length; i++) {
        // Image Grid
        if (msgText[i].includes("(Image)")) {
            const lineImgCount = msgText[i].split('(Image)').length - 1
            const num = imgCount + lineImgCount;
            let msgImg = [];
            let j = imgCount;
            while (j < num) {
                let currImg = j;
                msgImg.push(<img 
                    key={`artist-${artistNum}-img-${i}-${j}`} 
                    src={require(`../../../assets/fab/${artistNum}/media/${chatId.length === 1 ? "0" + chatId : chatId}_${j}.jpg`)} 
                    onClick={() => onOpenMedia(
                        true, 
                        require(`../../../assets/fab/${artistNum}/media/${chatId.length === 1 ? "0" + chatId : chatId}_${currImg}.jpg`),
                        `../../../assets/fab/${artistNum}/media/${chatId.length === 1 ? "0" + chatId : chatId}`, 
                        currImg, 
                        num
                    )} 
                    alt=""
                />);
                j++;
            }
            msgText[i] = <div key={`artist-${artistNum}-text-${i}`} className={`artist-msg msg-img-${lineImgCount}`}>{msgImg}</div>;
            imgCount = j;
        } else {
            if (msgText[i] === "") {
                msgText[i] = <p> </p>;
            } else {
                // Color and Size
                msgText[i] = styleText(msgText[i]);
                msgText[i] = <p key={`msgText-${i}`}>{msgText[i]}</p>;
            }
        }
    }
    return (
        <div className="msg-text">
            {msgText}
        </div>
    );
}

function MessagePage() {
    const location = useLocation();
    const navigate = useNavigate();

    const [ArtistNum, setArtistNum] = useState("");
    const [ProfileImg, setProfileImg] = useState("");
    const [ChatId, setChatId] = useState("");
    const [CSVText, setCSVText] = useState([]);
    const [ReplyCount, setReplyCount] = useState(0);
    const [Media, setMedia] = useState(null);
    const [OpenMedia, setOpenMedia] = useState(false);

    useEffect(() => {
        const artistNum = location.pathname.split('/')[2];
        const chatId = location.pathname.split('/')[3];

        setArtistNum(artistNum);
        setChatId(chatId);
        const profileImg = require(`../../../assets/fab/${artistNum}/profile/0.jpg`);
        setProfileImg(profileImg);
    }, [location.pathname]);

    useEffect(() => {
        if (ArtistNum === "" || ChatId === "") return;

        const papaConfig = {
            complete: (results, file) => {
                // console.log('Parsing complete:', results, file);
                let dataCSV = results.data;
                // Get text data by message
                for (let i = 0; i < dataCSV[0].length; i += 4) {
                    // Get text data by line
                    if (ChatId === dataCSV[0][i]) {
                        let dataByLine = [];
                        for (let j = 2; j < dataCSV.length; j++) {
                            if (dataCSV[j].slice(i, i + 4)[2] === '') { // Empty line
                                break;
                            } else {
                                dataByLine.push(dataCSV[j].slice(i, i + 4));
                            }
                        }
                        setCSVText(dataByLine[0]);
                        setReplyCount(countReply(dataCSV.slice(3, dataCSV.length), i));
                        break;
                    }
                }
            },
            download: true,
            error: (error, file) => {
                console.log('Error while parsing:', error, file);
            },
        };

        const textCSV = require(`../../../assets/fab/${ArtistNum}/text.csv`);
        readString(textCSV, papaConfig);
    }, [ArtistNum, ChatId]);

    // Media slide
    const onOpenMedia = (open, media, path, currImg, imgCount) => {
        if (open && path && path !== "") {
            console.log(path);
            setMedia({ media: media, path: path, currImg: currImg, imgCount: imgCount });
        } else {
            setMedia(null);
        }
        setOpenMedia(open);
    };

    const onChatPage = () => {
        navigate(`/fab/${ArtistNum}/${ChatId}/msg`);
    };

    return (
        <div className="mobile msgpage">
            <Header url={`/fab/${ArtistNum}/`} />
            <div className="body">
                {OpenMedia && Media && <MediaSlide openMedia={() => onOpenMedia()} media={Media} />}
                <div className="msg-body">
                    {ChatId !== "" && CSVText && CSVText.length > 0 && <Message chatId={ChatId} CSVText={CSVText} artistNum={ArtistNum} onOpenMedia={onOpenMedia} />}
                </div>
                <div className="msg-footer select-none">
                    <div className="footer-left">
                        <img src={ProfileImg} className="artist-profile" alt="" />
                        <div className="footer-text">
                            <p className="profile-name">{artistName[ArtistNum - 1]}</p>
                            {CSVText && CSVText.length > 0 && <p>{CSVText[3]}</p>}
                        </div>
                    </div>
                    <div className="footer-right">
                        <div className="footer-icon" onClick={onChatPage}><CommentOutlined /></div>
                        <p> ({ReplyCount})</p>
                    </div>
                </div>
            </div>
        </div>
    );
}

export default MessagePage;