import { Route, Routes } from 'react-router-dom';
import './App.css';
// Components
import NavBar from './components/NavBar/NavBar';
import HomePage from './components/HomePage/HomePage';
import NotFound from './components/NotFoundPage/NotFound';
// Fab
import FabArtistListPage from './components/Fab/ArtistListPage/ArtistListPage';
import FabArtistPage from './components/Fab/ArtistPage/ArtistPage';
import FabMessagePage from './components/Fab/MessagePage/MessagePage';
import FabChatPage from './components/Fab/ChatPage/ChatPage';
import FabSearchPage from './components/Fab/SearchPage/SearchPage';
// Vlive
import VliveChatListPage from './components/Vlive/ChatListPage/ChatListPage';
import VliveChatPage from './components/Vlive/ChatPage/ChatPage';
// Fromm
import FrommArtistListPage from './components/Fromm/ArtistListPage/ArtistListPage';
import FrommChatPage from './components/Fromm/ChatPage/ChatPage';

export const convertDate = (date) => {
  if (date === "") return "";
  let dateText = date.split(".");
  if (dateText[1].length < 2) dateText[1] = "0" + dateText[1];
  if (dateText[2].length < 2) dateText[2] = "0" + dateText[2];
  return `${dateText[0]} ${dateText[1]} ${dateText[2]}`;
};

function App() {
  return (
    <div className="App-bg">
      <div className="App">
        <NavBar />
        <Routes>
          <Route exact path='/' element={<HomePage />} />
          <Route path='/vlive' element={<VliveChatListPage />} />
          <Route path='/vlive/:chatDate' element={<VliveChatPage />} />
          <Route path='/fab' element={<FabArtistListPage />} />
          <Route path='/fab/:artistId' element={<FabArtistPage />} />
          <Route path='/fab/:artistId/:chatId' element={<FabMessagePage />} />
          <Route path='/fab/:artistId/:chatId/msg' element={<FabChatPage />} />
          <Route path='/fab/search' element={<FabSearchPage />} />
          <Route path='/fromm' element={<FrommArtistListPage />} />
          <Route path='/fromm/:artistId' element={<FrommChatPage />} />
          {/* <Route path='/fromm/:artistId/date' element={<FrommChatListPage />} /> */}
          <Route component={NotFound} />
        </Routes>
      </div>
    </div>
  );
}

export default App;