import React, { useEffect, useState } from 'react';
import './ChatPage.css';
import { parseDate } from '../../../lib/date';

const mentionedText = (text) => {
    if (!text.includes('@ ')) return text;
    let textList = text.split('@ ');
    if (textList[0].slice(-1) === '@') {
        return (
            <span>
                {`${textList[0]}@ `}
                {mentionedText(textList[1])}
            </span>
        )
    }
    return (
        <span>
            {textList[0]}
            <span className="mention select-none"> </span>
            {mentionedText(textList[1])}
        </span>
    );
}

function ChatBubble({ dateStr, artistNum, text, type }) {
    const [Date, setDate] = useState(null);
    const [ImageName, setImageName] = useState("");

    useEffect(() => {
        if (!dateStr) return;
        const date = parseDate(dateStr);
        setDate(parseDate(dateStr));
        setImageName(date.year.slice(2) + date.month + date.date)
    }, [dateStr]);

    // TODO: voice
    if (type === "Voice") {
        if (!dateStr) return <div>Loading...</div>
        // return (
        //     <div className="bubble voice audio">
        //         <audio src={require(`../../../assets/fromm/${artistNum}/voice/${text}.m4a`)} controls>
        //             Your browser does not support the audio element.
        //         </audio>
        //     </div>
        // );
    }
    
    // Image
    if (type === "Image") {
        if (!dateStr || ImageName === "") return <div>Loading...</div>
        const image = require(`../../../assets/fromm/${artistNum}/media/${ImageName}_${text}.PNG`);
        return (
            <div className="bubble image">
                <img src={image} alt="" />
            </div>
        );
    }

    return (
        <div className="bubble">{mentionedText(text)}</div>
    );
}

export default ChatBubble;
