import { useEffect, useState } from 'react';
import { useNavigate } from 'react-router-dom';
import Header from '../../Header/Header';
import './ArtistListPage.css';

const artistName = [
    "  HeeJin", "  HyunJin", "  HaSeul", "  YeoJin", 
    "  ViVi", "  Kim Lip", "  JinSoul", "  Choerry", 
    "  Yves", "  Chuu", "  Go Won", "   Olivia Hye"
];
const artists = [];
for (let i = 1; i < 13; i++) {
    try {
        // If artist exists
        let profileImg = require(`../../../assets/fab/${i}/profile/0.jpg`);
        artists.push({ 
            index: i,
            name: artistName[i - 1],
            profileImg: profileImg,
        });
    } catch (err) { } // If artist doesn't exist, do nothing 
}

/**
 * Search component.
 * @returns Search component
 */
function Search() {
    const navigate = useNavigate();
    
    const [Artist, setArtist] = useState("");
    const [SearchText, setSearchText] = useState("");

    // Set first artist to the Search Bar
    useEffect(() => {
        if (artists.length > 0 && artists[0].profileImg !== "") {
            setArtist(`${artists[0].index}`);
        }
    }, []);
    
    // Select artist to search replies
    const onSelect = (event) => {
        setArtist(event.target.value);
    };
    
    // Search fab replies
    const onSearch = () => {
        navigate(`/fab/search?member=${Artist}&text=${SearchText}`);
    };

    return (
        <div className="artist-search">
            <h2> </h2>
            <select onChange={onSelect}>
                {artists && artists.length > 0 && artists.map((artist, index) => (
                    <option key={`select-${index}`} value={artist.index}>{artist.name}</option>
                ))}
            </select>
            <div className="search-text">
                <input value={SearchText} onInput={(e) => setSearchText(e.target.value)} />
                <button onClick={onSearch}></button>
            </div>
        </div>
    );
}

/**
 * Artist list page component.
 * @returns Artist list page component
 */
function ArtistListPage() {
    const navigate = useNavigate();

    return (
        <div className="mobile fab">
            <Header url="/" />
            <div className="fab-artists-page flex-col flex-center">
                <div className="artist-list flex-col flex-center">
                    {artists && artists.length > 0 && artists.map((artist, index) => (
                        <img 
                            key={`artist-list-${index}`} 
                            src={artist.profileImg} 
                            className="artist-list-img" 
                            onClick={() => navigate(`/fab/${artist.index}`)}
                            alt=""
                        />
                    ))}
                </div>
                <Search />
            </div>
        </div>
    );
}

export default ArtistListPage;