import React, { useEffect, useState } from 'react';
import { readString } from 'react-papaparse';
import Header from '../../Header/Header';
// Style
import './ChatPage.css';
import ChatBubbleBlue from './ChatBubbleBlue';
import { useLocation } from 'react-router';
import { parseDate } from '../../../lib/date';

/**
 * Chat page component
 * @returns Chat page component
 */
function ChatPage() {
    const location = useLocation();
    
    let dateStr = "";
    let date = {};
    
    const [ChatList, setChatList] = useState([]);
    
    // Get date from param
    useEffect(() => {
        dateStr = location.pathname.split('/').pop();
        date = parseDate(dateStr);
    });

    // Fetch chat data on date
    useEffect(() => {
        if (date !== {}) {
            const textCSV = require(`../../../assets/vlive/text.csv`);
            readString(textCSV, papaConfig);
        }
    });

    // Helper function to read chat data on date
    const papaConfig = {
        complete: (results, file) => {
            let dataCSV = results.data;
            let chatList = [];
            for (let i = 1; i < dataCSV.length; i++) {
                if (dateStr === dataCSV[i][0]) {
                    const chatObj = {
                        date: date,
                        text: dataCSV[i][1],
                        type: dataCSV[i][2],
                    };
                    chatList.push(chatObj);
                } else if (chatList.length > 0) {
                    break;
                }
            }
            setChatList(chatList);
        },
        download: true,
        error: (error, file) => {
            console.log('Error while parsing:', error, file);
        },
    };

    // If date isn't loaded
    if (date === {}) {
        <div>Loading...</div>
    };
    
    return (
        <div className="mobile vlive vlive-chat-page">
            <Header url="/vlive" />
            <div className="flex-col chats">
                {ChatList && ChatList.length > 0 && ChatList.map((chat, index) => (
                    <ChatBubbleBlue key={`vlive-chat-bubble-${dateStr}-${index}`} date={chat.date} text={chat.text} type={chat.type} />
                ))}
            </div>
        </div>
    );
}

export default ChatPage;